*{
  padding:0;
  margin: 0;
  box-sizing: border-box;
}
html,#root,body{
  width:100%;
  overflow-x: hidden;
}
@media only screen and (max-width: 760px) {
  html,#root,body{
    width:100vw;
    overflow-x: hidden;
  }
}
body{
    font-family: 'Poppins', sans-serif;
    overflow-x: hidden;
  }
